<template>
    <div>
        <div class="item-reviews">
            <div class="reviews-title">商品评价（{{total}}）</div>
            <div v-show="reviewdata.length>0">
                <div class="reviews-wrap">
                    <div class="reviews-list" v-for="(item, index) in reviewdata" :key="index">
                        <div class="uinfo">
                            <div class="head">
                                <img data-v-a304f74e="" :alt="item.title" :src="item.head">
                            </div>
                            <div class="nickname">{{item.nickname}}</div>
                        </div>
                        <div class="reviews-content">{{item.content}}</div>
                        <div class="reviews-data">{{item.times}}</div>
                    </div>
                </div>
            </div>
            <div data-v-0cb1cf6a="" class="no-data" v-show="reviewdata.length<=0" style="">暂无评价！</div>
        </div>
    </div>
</template>

<script>
    import {mapActions, mapState} from "vuex"
    export default {
        data(){
            return {
                gid: this.$route.query.gid ? this.$route.query.gid : "",
            }
        },
        created(){
            //商品评价
            this.getReview({gid:this.gid});
        },
        computed: {
            ...mapState({
                //商品评价
                reviewdata:(state) => state.goods.reviewdata,
                //评价数量
                total:(state) => state.goods.total
            })
        },
        methods:{
            ...mapActions({
                //商品评价
                getReview:"goods/getReview"
            }),
        }
    }
</script>

<style scoped>
    .item-reviews {
        width: 100%;
        background-color: #fff;
        margin-top: .5rem;
        overflow: hidden;
        padding-bottom: 1.7rem;
    }
    .no-data {
        text-align: center;
        font-size: .32rem;
        margin-top: .3rem;
    }
    .item-reviews .reviews-title {
        color: #7b7f82;
        font-size: .32rem;
        width: 90%;
        margin: 0 auto;
        margin-top: .2rem;
    }
    .mask[data-v-a304f74e] {
        width: 100%;
        height: 100%;
        position: fixed;
        left: 0;
        top: 0;
        z-index: 11;
        background-color: rgba(0,0,0,.3);
    }
    .item-reviews .reviews-wrap {
        width: 90%;
        margin: 0 auto;
    }

    .item-reviews .reviews-wrap .reviews-list {
        width: 100%;
        margin-top: .4rem;
    }

    .reviews-wrap .reviews-list .uinfo {
        width: 100%;
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        display: -webkit-flex;
        height: .6rem;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        -webkit-align-items: center;
    }

    .reviews-wrap .reviews-list .reviews-content {
        width: 100%;
        margin: 0 auto;
        font-size: .28rem;
        margin-top: .2rem;
    }

    .reviews-list .uinfo .head {
        width: .6rem;
        height: .6rem;
        margin-right: .2rem;
    }

    .reviews-list .uinfo .head img {
        width: 100%;
        height: 100%;
        border-radius: 100%;
    }

    .reviews-list .uinfo .nickname {
        width: auto;
        font-size: .28rem;
    }

    .reviews-wrap .reviews-list .reviews-data {
        width: auto;
        font-size: .28rem;
        color: #7b7f82;
        margin-top: .2rem;
    }
</style>